      // 心率
      heartRateReport: function heartRateReport(val) {
            var lev = 0;
            console.log(val + "###");
            if (val.toString().indexOf("%") > 0) {
                  lev = parseFloat(val.replace('%', ''));
            } else {
                  lev = parseFloat(val);
            }

            var backString = "";
            var status = "";

            if (lev < 60) {
                  backString = "心动过缓，可见于长期从事重体力劳动的健康人和运动员；或见于甲状腺机能低下、颅内压增高、阻塞性黄疸以及洋地黄、奎尼丁或心得安类药物过量。如果心率低于40次/分钟，应考虑有病态窦房结综合征、房室传导阻滞等情况。如果脉搏强弱不等、不齐且脉率少于心率，应考虑心房纤颤";
                  status = "心动过缓";
            } else if (lev >= 60 && lev <= 100) {
                  backString = "心率正常";
                  status = "心率正常";
            } else if (lev > 100) {
                  backString = "心动过速，常见于兴奋、激动、吸烟、饮酒、喝浓茶或咖啡后，或见于感染、发热、休克、贫血、缺氧、甲亢、心力衰竭等病理状态下，或见于应用阿托品、肾上腺素、麻黄素等药物后";
                  status = "心动过速";
            }

            var backObject = new Object();
            backObject.report = backString;
            backObject.status = status;

            return backObject;
      },

