
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Toast from 'vux/src/components/toast/index.vue'


import tools from '../../../lib/tools.js';
import health from '../../../lib/healthy';
import echarts from 'echarts';
import axios from 'axios';
export default {
name:'SixEquipment',
components:{ Flexbox, FlexboxItem,Toast},
data() {
return {
fildData: {},
type:'',
startValue:'',
hrUrl:''
}
},
created(){
},
methods: {

drawGraph(id) {

this.chart = echarts.init(document.getElementById(id));

this.chart.showLoading();

let that = this;
let type = tools.getRequestParam("type");
that.type=type;
let managerId = (localStorage.getItem('md') || tools.getCookie('user_id'));

this.$http.axios({method:'get',url:'/deviceData/getMeasureDataByType?managerId='+managerId+'&time=&type='+type}).then((result) => {
let data = result.data;

if(data.successful && (data.status==200)){
that.fildData = data.data;
let showLength = that.fildData.xAxisArray.length;
console.log(showLength);
if(showLength > 15){
that.startValue = that.fildData.xAxisArray[parseInt(showLength)-15];
console.log(that.fildData.xAxisArray[parseInt(showLength)-15]);
}else{
that.startValue ="";
}
let params =new Object();

let xAxisIndex = parseInt(that.fildData.xAxisArray.length)-1;
params.name = that.fildData.xAxisArray[xAxisIndex];
console.log(params.name);
if(type == "BMI"){
that.fildData.name = that.fildData.name.toLocaleUpperCase();
}
if(type == "BP"){
let yAxisArray = that.fildData.yAxisArray;
let val = that.fildData.value;
let bpVal = val.split("/")[0];
let spVal = val.split("/")[1];
that.$set(that.fildData,'bp',bpVal);
that.$set(that.fildData,'sp',spVal);

let series = [
{
name: '收缩压',
type: 'line',
itemStyle : {
normal:{
lineStyle:{
color:'#3399cc'
}
/*,areaStyle: {type: 'default'}*/
}
},showAllSymbol: true,
symbolSize : 8,
data:yAxisArray.dbp
},
{
name: '舒张压',
type: 'line',
itemStyle : {
normal:{
lineStyle:{
color:'#3399cc'
}
/*,areaStyle: {type: 'default'}*/
}
},
showAllSymbol: true,
symbolSize : 8,
data:yAxisArray.sbp
}
];
that.fildData.yAxisArray =series;
}else{
let yAxisIndex = parseInt(that.fildData.yAxisArray.length)-1;
params.value = that.fildData.yAxisArray[yAxisIndex];
let series = [
{
name: that.fildData.name,
type: 'line',
itemStyle : {
normal:{
lineStyle:{
color:'#3399cc'
}
/*,areaStyle: {type: 'default'}*/
}
},showAllSymbol: true,
symbolSize : 8,
data:that.fildData.yAxisArray
}
];
that.fildData.yAxisArray =series;
if(type == "BMI"){
series.name = "体重";
}
}
this.bindClick();
this.setOptions();
that.setData(params,that);
}else{
that.showToast = true;
that.toastMsg = data.resultCode.message;
}
},(error) => {
console.log("进入error");
console.log(error);
});
/*that.fildData = data.data;*/
},
bindClick(){
let that = this;
this.chart.on('click', function (params) {
that.setData(params,that);
})
},
setData(params,that){
let val = params.value;
that.fildData.value = val?val:that.fildData.value;
switch (that.type){
case "BMI"://体重
let index_bmi = tools.getArrIndex(params.name,that.fildData.xAxisArray);
console.log(params.name);
let hieght = that.fildData.heightArray[index_bmi];//获取身高
that.$set(that.fildData,'plan',health.weightPlan());
let bmiPlan = health.weightHandle(hieght,val);
that.fildData.status = bmiPlan.status;
that.$set(that.fildData,'normalWeight',(bmiPlan.loWeight+"-"+bmiPlan.hiWeight));
that.$set(that.fildData,'bmi',bmiPlan.bmi);
that.fildData.proposal = bmiPlan.report;
break;
case "BP"://血压
let index = tools.getArrIndex(params.name,that.fildData.xAxisArray);
let bp = that.fildData.yAxisArray[0].data[index];//收缩压
let sp = that.fildData.yAxisArray[1].data[index];//舒张压
that.$set(that.fildData,'bp',bp);
that.$set(that.fildData,'sp',sp);
console.log(bp+"###"+sp);
that.$set(that.fildData,'plan',health.bloodPressurePlan());
let bloodPressure = health.bloodPressureReport(sp,bp);
that.fildData.status = bloodPressure.status;
that.fildData.proposal = bloodPressure.report;
break;
case "BO"://血氧
that.$set(that.fildData,'plan',health.bloodOxygenPlan());
let bloodOxygen = health.bloodOxygenReport(val);
that.fildData.status = bloodOxygen.status;
that.fildData.proposal = bloodOxygen.report;
break;
case "HR"://心率
let index_hr = tools.getArrIndex(params.name,that.fildData.xAxisArray);
that.hrUrl = that.fildData.hrUrlArray[index_hr];
that.$set(that.fildData,'plan',health.heartRatePlan());
let heartRate = health.heartRateReport(val);
that.fildData.status = heartRate.status;
that.fildData.proposal = heartRate.report;
break;
case "BG"://血糖
that.$set(that.fildData,'plan',health.bloodSugarPlan());
let bloodSugar = health.bloodSugarReport(val);
that.fildData.status = bloodSugar.status;
that.fildData.proposal = bloodSugar.report;
break;
}
/*数据条加载*/
if(that.type != "BP"){
if(that.type!= "SN"){
let position = 0;//数值所在位置变量
console.log("加载数据条");
let plan = that.fildData.plan;
let lenght = that.fildData.plan.length;//数值my_number  //标准criterion
for (let key of Object.keys(plan)) {
console.log(plan[key].conclusion+"#"+that.fildData.status);
if(plan[key].conclusion == that.fildData.status){
position = parseInt(key);
}
}
if(position == lenght){
position = position*0.8;
}
if(position == 0){
position = 0.5
}
position = tools.getPercent(position,lenght);
position = parseInt(position)+parseInt('5%')+"%";
let percent = tools.getPercent(1,lenght);

var timer = setTimeout(function(){
let criterions = document.querySelectorAll('.criterionItem');
for(let i=0;i< criterions.length;i++){
criterions[i].style.width =percent;
}
let my_number = document.querySelectorAll(".my_number")[0];
console.log(position);
my_number.querySelectorAll("span")[0].style.marginLeft=position;
my_number.querySelectorAll("em")[0].style.left= 'calc('+position+'+ 0.8rem)';
},0);
}
}else{
console.log("加载数据条");
let position_bp = 0;//数值所在位置变量 //收缩压加载数据
let plan_bp = that.fildData.plan[0];
let lenght_bp = plan_bp.length;//数值my_number  //标准criterion

for (let key of Object.keys(plan_bp)) {
if(plan_bp[key].conclusion == that.fildData.status){
console.log(plan_bp[key].conclusion+"$$"+that.fildData.status);
position_bp = parseInt(key);
}
}
console.log(position_bp+"%%%%%%%%%%%%%%%%%%%"+lenght_bp);
if(position_bp == lenght_bp){
position_bp = position_bp*0.8;
}
if(position_bp == 0){
position_bp = 0.5
}
position_bp = tools.getPercent(position_bp,lenght_bp);//数值的位置
position_bp = parseInt(position_bp)+parseInt('5%')+"%";
let percent_bp = tools.getPercent(1,lenght_bp);//标准的宽度

setTimeout(function(){
let my_number_bp = document.getElementById("my_number_bp");
let criterions = document.getElementById('criterion_bp').querySelectorAll(".criterionItem");
for(let i=0;i< criterions.length;i++){
criterions[i].style.width =percent_bp;
}
console.log(criterions);
my_number_bp.querySelectorAll("span")[0].style.marginLeft=position_bp;
},0);


let position_sp = 0;//数值所在位置变量   //舒张压加载数据
let plan_sp = that.fildData.plan[1];
let lenght_sp = plan_bp.length;//数值my_number  //标准criterion
for (let key of Object.keys(plan_sp)) {
if(plan_sp[key].conclusion == that.fildData.status){
position_sp = parseInt(key);
}
}
if(position_sp == lenght_sp){
position_sp = position_sp*0.8;
}
if(position_sp == 0){
position_sp = 0.5
}
position_sp = tools.getPercent(position_sp,lenght_sp);//数值的位置
position_sp = parseInt(position_sp)+parseInt('5%')+"%";
let percent_sp = tools.getPercent(1,lenght_sp);//标准的宽度

setTimeout(function(){
let criterions = document.getElementById('criterion_sp').querySelectorAll(".criterionItem");
for(let i=0;i< criterions.length;i++){
criterions[i].style.width =percent_sp;
}
let my_number_sp = document.getElementById("my_number_sp");
my_number_sp.querySelectorAll("span")[0].style.marginLeft=position_sp;
},0);
}
},
setOptions(){
let that = this;

this.chart.setOption({
backgroundColor: '#ffffff',//背景色
tooltip : {
trigger: 'item',
axisPointer: {
type: 'cross',
animation: false,
label: {
backgroundColor: '#505765'
}
}
},
xAxis: [{
axisLabel:{
show: false
},
axisTick:{
show: false
},
splitLine: {
show: false
},
axisLine:{
show: false
},
data: that.fildData.xAxisArray
}],
yAxis: {
axisLabel:{margin:-45},
splitLine: {
show: false
},
axisTick:{
show: false
},
axisLine:{
show: false
}
},
grid: {
width: '100%',
height: '80%',
y:'10%',
x:'0'
},
dataZoom: [{
startValue:that.startValue,
show: false
}, {
type: 'inside'
}],
series: that.fildData.yAxisArray
});
this.chart.hideLoading()
}
},


mounted() {
this.drawGraph('echarts')
this.$nextTick(function() {

})
}
}



