
import InlineCalendar from 'vux/src/components/inline-calendar/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import tools from '../../../lib/tools.js';
import recordsBox from '../../../components/RecordsBox.vue';
console.log(tools.simpleDateFormat(new Date(),'yyyy-MM').split('-'))
export default {
name:'ruEquipment',
props:['message'],
components:{InlineCalendar,recordsBox,Datetime,Toast},
data(){
return {
msg:'默认值',
toDay:tools.simpleDateFormat(new Date(),'yyyy-MM-dd'),
selectTimer:'',
detailMonth:[],
month:[],
records:[],
titleDate:'',
showToast:false,
toastMsg:'',
monthDay:''
}
},
created(){
console.log(111);
/*this.getNumberMouth();*/
this.selectTimer = tools.simpleDateFormat(new Date(),'yyyy-MM-dd');
},
watch:{
selectTimer(n,o){


this.getNumberMouth(n);
}
},
methods:{
sendParentMsg:function(){
this.$emit('listenToChild','我这里是子组件发出的信息')
},
getMouth(){
let vm = this;
let date = tools.simpleDateFormat(new Date(),'yyyy-MM');
this.$vux.datetime.show({
cancelText: '取消',
confirmText: '确定',
format: 'YYYY-MM',
value: date,
onConfirm (val) {
val = val.split('-');
vm.titleDate = vm.formatValue(val);  //给title赋值
let newArr = [];
newArr.push(val[0]*1);
newArr.push(val[1]*1);
vm.detailMonth = newArr;   // 给页面date框架赋值
}
})
},
getNumberMouth(time){
let vm = this;
let date = tools.simpleDateFormat(time || new Date(),'yyyy-MM-dd');
let times = tools.simpleDateFormat(time || new Date(),'yyyy-MM-dd');
date = date.split('-');
this.monthDay = date[1]+"月"+date[2]+"日";
let toDay=tools.simpleDateFormat(new Date(),'yyyy-MM-dd');//获取当前日期
let managerId = (localStorage.getItem('md') || tools.getCookie('user_id'));
this.titleDate = this.formatValue(date);
let data = {"managerId": managerId,"time": times};

this.$http.axios({method:'get',url:'/deviceData/getMeasureDataByMidType?managerId='+managerId+'&time='+times+'&type=RU'}).then((result) => {
console.log(JSON.stringify(result));
result = result.data;
console.log(JSON.stringify(result));
if(result.successful && (result.status==200)){
vm.records = result.data[0].value;
console.log(JSON.stringify(vm.records));
if(vm.records.urine_leukocytes!=null && typeof(vm.records.urine_leukocytes) !="undefind" ){
if(-1<=vm.records.urine_leukocytes<=7){
vm.$set(vm.records,'urine_leukocytes_string',"阴性");
}else{
vm.$set(vm.records,'urine_leukocytes_string',"阳性");
}
}else{
vm.records.urine_leukocytes = "-";
}
if(-1<=vm.records.urine_nitrite<=7){
vm.$set(vm.records,'urine_nitrite_string',"阴性");
}else{
vm.$set(vm.records,'urine_nitrite_string',"阳性");
}
if(-1<=vm.records.urobilinogen<=7){
vm.$set(vm.records,'urobilinogen_string',"阴性");
}else{
vm.$set(vm.records,'urobilinogen_string',"阳性");
}
if(-1<=vm.records.urine_protein<=7){
vm.$set(vm.records,'urine_protein_string',"阴性");
}else{
vm.$set(vm.records,'urine_protein_string',"阴性");
}
if(-1<=vm.records.urine_occult_blood<=7){
vm.$set(vm.records,'urine_occult_blood_string',"阴性");
}else{
vm.$set(vm.records,'urine_occult_blood_string',"阳性");
}
if(-1<=vm.records.urine_ketone<=7){
vm.$set(vm.records,'urine_ketone_string',"阴性");
}else{
vm.$set(vm.records,'urine_ketone_string',"阳性");
}
if(-1<=vm.records.urine_bilirubin<=7){
vm.$set(vm.records,'urine_bilirubin_string',"阴性");
}else{
vm.$set(vm.records,'urine_bilirubin_string',"阳性");
}
if(-1<=vm.records.urine_glucose<=7){
vm.$set(vm.records,'urine_glucose_string',"阴性");
}else{
vm.$set(vm.records,'urine_glucose_string',"阳性");
}
if(-1<=vm.records.urine_VitC<=7){
vm.$set(vm.records,'urine_VitC_string',"阴性");
}else{
vm.$set(vm.records,'urine_VitC_string',"阳性");
}
}else{
vm.showToast = true;
vm.toastMsg = result.resultCode.message;
console.log(vm.toastMsg);
}
},(error) => {
console.log(error);
});
},
formatValue(val) {
return val[0]+'年'+val[1]+'月';
},
goBack(){
let url = sessionStorage.getItem('tempURL');
tools.next(url || '/m/healthRecords.html');
}
}
}



