import tools from '../lib/tools.js';
import healthy from '../lib/healthy.js';
export default {
    name:'recordsBox',
    props:['serviceList'],
    data(){
        return {
            downArr:['偏瘦','疑似低血压','心动过缓','血糖偏低',"偏低","异常","轻度异常"],
            upArr:['超重偏胖','肥胖','疑似高血压','心动过速','血糖偏高',"偏高","高","较高"],
            abnormalArr:["血压异常","严重","轻度"]
        }
    },
    methods:{
        judgeVal(status){
            if((this.downArr.indexOf(status)>=0) || (this.upArr.indexOf(status)>=0) || (this.abnormalArr.indexOf(status)>=0)){
                return true;
            }
            return false;
        },
        goTo(type){
            // window.location.href="healthFileData.html?type="+type;
            tools.next("healthFileData.html?type="+type);
        },
        getRu(value){
            let backObject = healthy.ruReport(value);
            if(backObject.report == '异常'){
                return "<span style='color: #EB6160'>"+backObject.report+"</span>";
            }else {
                return "<span>"+backObject.report+"</span>";
            }

        },
        getLogoImg(name){
            switch (name) {
                case '血压':
                    return '/m/static/img/gaoxueya3x.png';
                case '血糖':
                    return '/m/static/img/xuetang3x.png';
                case '心率':
                    return '/m/static/img/xinlv3x.png';
                case 'BMI':
                    return '/m/static/img/tizhong3x.png';
                case '血氧':
                    return '/m/static/img/xueyang3x.png';
                case '步数':
                    return '/m/static/img/bushu3x.png';
                case '血红蛋白':
                    return '/m/static/img/xuehongdanbai3x.png';
                case '尿酸':
                    return '/m/static/img/niaosuan3x.png';
                case '水分比':
                    return '/m/static/img/shuifenbi.png';
                case '肌肉':
                    return '/m/static/img/jirou.png';
                case '骨量':
                    return '/m/static/img/guzhiliang.png';
                case '基础代谢率':
                    return '/m/static/img/daixie.png';
                case '蛋白质':
                    return '/m/static/img/danbai.png';
                case '体脂':
                    return '/m/static/img/tizhi.png';
                case '总胆固醇':
                    return '/m/static/img/zongdanguchun.png';
                case '尿常规':
                    return '/m/static/img/niaochanggui.png';
                case '体温':
                    return '/m/static/img/tiwen.png';
                case '血脂五项':
                    return '/m/static/img/xuezhi.png';
                case '糖化血红蛋白':
                    return '/m/static/img/tanghuadanbai.png';
                case '全量程C反应蛋白':
                    return '/m/static/img/quanliangcheng.png';

                default:
                    break;
            }
        }
    }

}



