import Vue from 'vue';
import Router from 'vue-router';

Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/bpEquipment',
            name: 'BP',
            component:require('../components/BpEquipment')
        },{
            path: '/sixEquipment',
            name: 'SIX',
            component:require('../components/SixEquipment')
        },{
            path: '/uaEquipment',
            name: 'UA',
            component:require('../components/UaEquipment')
        },{
            path: '/bhbEquipment',
            name: 'BHB',
            component:require('../components/BhbEquipment')
        },{
            path: '/ruEquipment',
            name: 'RU',
            component:require('../components/RuEquipment')
        }
    ]
})



