
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Toast from 'vux/src/components/toast/index.vue'


import tools from '../../../lib/tools.js';
import health from '../../../lib/healthy';
import echarts from 'echarts';
import axios from 'axios';
export default {
name:'UaEquipment',
components:{ Flexbox, FlexboxItem,Toast},
data() {
return {
fildData: {},
type:'',
startValue:'',
hrUrl:''
}
},
created(){
},
methods: {

drawGraph(id) {

this.chart = echarts.init(document.getElementById(id));

this.chart.showLoading();

let that = this;
let type = tools.getRequestParam("type");
that.type=type;
let managerId = (localStorage.getItem('md') || tools.getCookie('user_id'));

this.$http.axios({method:'get',url:'/deviceData/getMeasureDataByType?managerId='+managerId+'&time=&type='+type}).then((result) => {
let data = result.data;

if(data.successful && (data.status==200)){
that.fildData = data.data;
let showLength = that.fildData.xAxisArray.length;
console.log(showLength);
if(showLength > 15){
that.startValue = that.fildData.xAxisArray[parseInt(showLength)-15];
console.log(that.fildData.xAxisArray[parseInt(showLength)-15]);
}else{
that.startValue ="";
}
let params =new Object();

let xAxisIndex = parseInt(that.fildData.xAxisArray.length)-1;
params.name = that.fildData.xAxisArray[xAxisIndex];
console.log(params.name);
let yAxisIndex = parseInt(that.fildData.yAxisArray.length)-1;
params.value = that.fildData.yAxisArray[yAxisIndex];
let series = [
{
name: that.fildData.name,
type: 'line',
itemStyle : {
normal:{
lineStyle:{
color:'#3399cc'
}
/*,areaStyle: {type: 'default'}*/
}
},showAllSymbol: true,
symbolSize : 8,
data:that.fildData.yAxisArray
}
];
that.fildData.yAxisArray =series;
/*if(type == "BMI"){
series.name = "体重";
}*/
this.bindClick();
this.setOptions();
that.setData(params,that);
}else{
that.showToast = true;
that.toastMsg = data.resultCode.message;
}
},(error) => {
console.log("进入error");
console.log(error);
});
/*that.fildData = data.data;*/
},
bindClick(){
let that = this;
this.chart.on('click', function (params) {
that.setData(params,that);
})
},
setData(params,that){
let val = params.value;
that.fildData.value = val?val:that.fildData.value;
switch (that.type){
case "UA"://尿酸
that.$set(that.fildData,'plan',health.uaPlan());
let uaReport = health.uaReport(val);
that.fildData.status = uaReport.status;
that.fildData.proposal = uaReport.report;
break;
case "TE"://体温
that.$set(that.fildData,'plan',health.tempPlan());
let tempReport = health.tempReport(val);
that.fildData.status = tempReport.status;
that.fildData.proposal = tempReport.report;
break;
case "WP"://水分
that.$set(that.fildData,'plan',health.wpPlan());
let wpReport = health.wpReport(val);
that.fildData.status = wpReport.status;
that.fildData.proposal = wpReport.report;
break;
case "BONE"://骨量
that.$set(that.fildData,'plan',health.bonePlan());
let boneReport = health.boneReport(val);
that.fildData.status = boneReport.status;
that.fildData.proposal = boneReport.report;
break;
case "BMR"://代谢
that.$set(that.fildData,'plan',health.bmrPlan());
let bmrReport = health.bmrReport(val);
that.fildData.status = bmrReport.status;
that.fildData.proposal = bmrReport.report;
break;
case "CHOL"://总胆固醇
that.$set(that.fildData,'plan',health.cholPlan());
console.log(that.fildData.plan+"查看plan")
let cholReport = health.cholReport(val);
that.fildData.status = cholReport.status;
that.fildData.proposal = cholReport.report;
break;
case "BF"://血脂四项
let index_bmi = tools.getArrIndex(params.name,that.fildData.xAxisArray);
that.fildData.TG = that.fildData.TGAxisArray[index_bmi];//获取甘油三酯
that.fildData.HDL = that.fildData.HDLAxisArray[index_bmi];//获取高密度脂蛋白胆固醇
that.fildData.LDL = that.fildData.LDLAxisArray[index_bmi];//获取低密度脂蛋白胆固醇
that.$set(that.fildData,'plan',health.cholPlan());
let cholReportAll = health.cholReport(val);
that.fildData.status = cholReportAll.status;
that.fildData.proposal = cholReportAll.report;
break;
case "HbA1c"://糖化血红蛋白
that.$set(that.fildData,'plan',health.hbalPlan());
let hbaReport = health.hbaReport(val);
that.fildData.status = hbaReport.status;
that.fildData.proposal = hbaReport.report;
break;
case "CRP"://全量程C反应蛋白
let index_crp = tools.getArrIndex(params.name,that.fildData.xAxisArray);
that.fildData.CRP = that.fildData.hsCrpAxisArray[index_crp];//获取C反应蛋白
that.$set(that.fildData,'plan',health.hscrpPlan());
let hscrpReport = health.hscrpReport(val);
that.fildData.status = hscrpReport.status;
that.fildData.proposal = hscrpReport.report;
break;
}
/*数据条加载*/
let position = 0;//数值所在位置变量
console.log("加载数据条");
let plan = that.fildData.plan;
let lenght = that.fildData.plan.length;//数值my_number  //标准criterion
for (let key of Object.keys(plan)) {
console.log(plan[key].conclusion+"#"+that.fildData.status);
if(plan[key].conclusion == that.fildData.status){
position = parseInt(key);
}
}
if(position == lenght){
position = position*0.8;
}
if(position == 0){
position = 0.5
}
position = tools.getPercent(position,lenght);
position = parseInt(position)+parseInt('5%')+"%";
let percent = tools.getPercent(1,lenght);

var timer = setTimeout(function(){
let criterions = document.querySelectorAll('.criterionItem');
for(let i=0;i< criterions.length;i++){
criterions[i].style.width =percent;
}
let my_number = document.querySelectorAll(".my_number")[0];
console.log(position);
my_number.querySelectorAll("span")[0].style.marginLeft=position;
my_number.querySelectorAll("em")[0].style.left= 'calc('+position+'+ 0.8rem)';
},0);
},
setOptions(){
let that = this;

this.chart.setOption({
backgroundColor: '#ffffff',//背景色
tooltip : {
trigger: 'item',
axisPointer: {
type: 'cross',
animation: false,
label: {
backgroundColor: '#505765'
}
}
},
xAxis: [{
axisLabel:{
show: false
},
axisTick:{
show: false
},
splitLine: {
show: false
},
axisLine:{
show: false
},
data: that.fildData.xAxisArray
}],
yAxis: {
axisLabel:{margin:-45},
splitLine: {
show: false
},
axisTick:{
show: false
},
axisLine:{
show: false
}
},
grid: {
width: '100%',
height: '80%',
y:'10%',
x:'0'
},
dataZoom: [{
startValue:that.startValue,
show: false
}, {
type: 'inside'
}],
series: that.fildData.yAxisArray
});
this.chart.hideLoading()
}
},


mounted() {
this.drawGraph('echarts')
this.$nextTick(function() {

})
}
}



