
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Toast from 'vux/src/components/toast/index.vue'

import intelligentServiceList from '../../components/IntelligentServiceList';
import tools from '../../lib/tools.js'
export default {
name: 'HealthFileData',
components: {Flexbox, FlexboxItem,Toast,intelligentServiceList},
data() {
return {
}
},
created(){

},
methods:{
rounte(){
let that = this;
let type = tools.getRequestParam("type");
that.type=type;
console.log(type);
if(type == "BP"){//血压
that.$refs.BP.click();
}else if(type == "BMI" || type == "BO" || type == "HR" || type == "SN" || type == "BG"){//BMI,血氧,心率,步数,血糖
that.$refs.SIX.click();

}else if(type == "UA" || type == "TE" || type == "WP" || type == "BONE" || type == "BMR" || type == "CHOL" || type == "BF" || type == "HbA1c" || type == "CRP"){
that.$refs.UA.click();
}else if(type == "BHB" || type == "MUCALE" || type == "PIW" || type == "FP"){//血红蛋白，肌肉，蛋白量，体脂
that.$refs.BHB.click();
}else if(type == "RU"){//尿常规
that.$refs.RU.click();
}
}
},
mounted() {
this.rounte();
}
}



